body {
  margin: 0;
  padding: 0;
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: var(--primary-color);
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

address {
  font-style: normal;
}

ul {
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Helvetica Neue", sans-selif;
  margin-top: 0;
}

a {
  display: inline-block;
}
